'use client'

import { useState, useEffect } from 'react'
import Link from 'next/link'

interface Customer {
  m: string
  etternavn: string
  adresse: string
  sted: string
  kommune: string
  vår: string
  høst: string
  epost: string
  fakturert: string
  mailVår: string
  mailHøst: string
  tidsbrukSlått: string
  fornyelsesdato: string
  sum: string
  notat: string
}

export default function DashboardPage() {
  const [recentCustomers, setRecentCustomers] = useState<Customer[]>([])
  const [upcomingInspections, setUpcomingInspections] = useState<Customer[]>([])
  const [greeting, setGreeting] = useState('')
  const userName = 'Edgar' // Dette kan senere hentes fra en brukerkontekst eller API

  useEffect(() => {
    const savedCustomers = localStorage.getItem('customers')
    if (savedCustomers) {
      const allCustomers = JSON.parse(savedCustomers)
      const sortedByRenewal = allCustomers
        .sort((a: Customer, b: Customer) => 
          new Date(a.fornyelsesdato).getTime() - new Date(b.fornyelsesdato).getTime()
        )
        .slice(0, 5)
      setUpcomingInspections(sortedByRenewal)

      const sortedByUpdate = allCustomers
        .sort((a: Customer, b: Customer) => 
          new Date(b.fornyelsesdato).getTime() - new Date(a.fornyelsesdato).getTime()
        )
        .slice(0, 5)
      setRecentCustomers(sortedByUpdate)
    }

    const getGreeting = () => {
      const hour = new Date().getHours()
      
      if (hour >= 5 && hour < 12) {
        return 'God morgen'
      } else if (hour >= 12 && hour < 17) {
        return 'God ettermiddag'
      } else if (hour >= 17 && hour < 22) {
        return 'God kveld'
      } else {
        return 'God natt'
      }
    }

    setGreeting(getGreeting())
  }, [])

  return (
    <div className="min-h-screen bg-gray-900 p-8 ml-64">
      <div className="max-w-7xl mx-auto">
        <h1 className="text-4xl font-bold text-white">{greeting}, {userName}</h1>
        
        {/* Header */}
        <div className="mb-12">
          <h1 className="text-4xl font-bold text-white">Dashboard</h1>
          <p className="text-gray-400 mt-2">Oversikt over aktiviteter og status</p>
        </div>

        {/* Statistikk-kort */}
        <div className="grid grid-cols-1 md:grid-cols-4 gap-6 mb-8">
          <div className="bg-gray-800 rounded-2xl p-6 border border-gray-700">
            <div className="flex items-center justify-between">
              <h3 className="text-gray-400">Aktive prosjekter</h3>
              <div className="w-10 h-10 rounded-full bg-blue-900/50 flex items-center justify-center">
                <svg className="w-5 h-5 text-blue-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 11H5m14 0a2 2 0 012 2v6a2 2 0 01-2 2H5a2 2 0 01-2-2v-6a2 2 0 012-2m14 0V9a2 2 0 00-2-2M5 11V9a2 2 0 012-2m0 0V5a2 2 0 012-2h6a2 2 0 012 2v2M7 7h10" />
                </svg>
              </div>
            </div>
            <p className="text-2xl font-bold text-white mt-2">12</p>
          </div>

          <div className="bg-gray-800 rounded-2xl p-6 border border-gray-700">
            <div className="flex items-center justify-between">
              <h3 className="text-gray-400">Timer denne uken</h3>
              <div className="w-10 h-10 rounded-full bg-green-900/50 flex items-center justify-center">
                <svg className="w-5 h-5 text-green-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 8v4l3 3m6-3a9 9 0 11-18 0 9 9 0 0118 0z" />
                </svg>
              </div>
            </div>
            <p className="text-2xl font-bold text-white mt-2">37.5</p>
          </div>

          <div className="bg-gray-800 rounded-2xl p-6 border border-gray-700">
            <div className="flex items-center justify-between">
              <h3 className="text-gray-400">Ventende oppgaver</h3>
              <div className="w-10 h-10 rounded-full bg-yellow-900/50 flex items-center justify-center">
                <svg className="w-5 h-5 text-yellow-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 8v4l3 3m6-3a9 9 0 11-18 0 9 9 0 0118 0z" />
                </svg>
              </div>
            </div>
            <p className="text-2xl font-bold text-white mt-2">8</p>
          </div>

          <div className="bg-gray-800 rounded-2xl p-6 border border-gray-700">
            <div className="flex items-center justify-between">
              <h3 className="text-gray-400">Kunder</h3>
              <div className="w-10 h-10 rounded-full bg-purple-900/50 flex items-center justify-center">
                <svg className="w-5 h-5 text-purple-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M17 20h5v-2a3 3 0 00-5.356-1.857M17 20H7m10 0v-2c0-.656-.126-1.283-.356-1.857M7 20H2v-2a3 3 0 015.356-1.857M7 20v-2c0-.656.126-1.283.356-1.857m0 0a5.002 5.002 0 019.288 0M15 7a3 3 0 11-6 0 3 3 0 016 0zm6 3a2 2 0 11-4 0 2 2 0 014 0zM7 10a2 2 0 11-4 0 2 2 0 014 0z" />
                </svg>
              </div>
            </div>
            <p className="text-2xl font-bold text-white mt-2">24</p>
          </div>
        </div>

        {/* Aktivitetsliste */}
        <div className="bg-gray-800 rounded-2xl p-6 border border-gray-700">
          <h2 className="text-xl font-semibold text-white mb-4">Nylige aktiviteter</h2>
          <div className="space-y-4">
            {[1, 2, 3].map((item) => (
              <div key={item} className="flex items-center justify-between py-3 border-b border-gray-700 last:border-b-0">
                <div className="flex items-center space-x-4">
                  <div className="w-10 h-10 rounded-full bg-gray-700 flex items-center justify-center">
                    <svg className="w-5 h-5 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 8v4l3 3m6-3a9 9 0 11-18 0 9 9 0 0118 0z" />
                    </svg>
                  </div>
                  <div>
                    <p className="text-white">Ny timeføring registrert</p>
                    <p className="text-sm text-gray-400">Prosjekt: Kundeportal</p>
                  </div>
                </div>
                <span className="text-sm text-gray-400">2 timer siden</span>
              </div>
            ))}
          </div>
        </div>

        {/* Kalender */}
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6 mt-8">
          <div className="bg-gray-800 rounded-2xl p-6 border border-gray-700">
            <h2 className="text-xl font-semibold text-white mb-4">Dagens plan</h2>
            <div className="space-y-4">
              {[1, 2, 3].map((item) => (
                <div key={item} className="flex items-center space-x-4">
                  <div className="w-2 h-2 rounded-full bg-blue-500"></div>
                  <div>
                    <p className="text-white">Møte med kunde</p>
                    <p className="text-sm text-gray-400">10:00 - 11:00</p>
                  </div>
                </div>
              ))}
            </div>
          </div>

          <div className="bg-gray-800 rounded-2xl p-6 border border-gray-700">
            <h2 className="text-xl font-semibold text-white mb-4">Ventende oppgaver</h2>
            <div className="space-y-4">
              {[1, 2, 3].map((item) => (
                <div key={item} className="flex items-center justify-between">
                  <div className="flex items-center space-x-4">
                    <input type="checkbox" className="rounded border-gray-700 text-blue-600 focus:ring-blue-500" />
                    <span className="text-white">Oppdatere dokumentasjon</span>
                  </div>
                  <span className="text-sm text-gray-400">Frist: 15. mars</span>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>
    </div>
  )
} 